export default (state, action) => {
  switch (action.type) {
    case "RIGHT ANSWER":
      console.log(action.payload);

      return {
        ...state,
        score: state.score + 1,
        questions: state.questions.filter(
          (question) => parseInt(question.id) != action.payload
        ),
        questionNumber: state.questionNumber + 1,
      };
    case "WRONG ANSWER":
      return {
        ...state,
        questions: state.questions.filter(
          (question) => parseInt(question.id) !== action.payload
        ),
        questionNumber: state.questionNumber + 1,
      };
    default:
      return state;
  }
};
